interface CategorySubFormProps {
	formHeader: React.ReactNode | null;
	subFormNode: object | null;
	rows: any[];
	setRowMetadata?: any;
	setRowLimits?: any;
	setRowSelection?: any;
	setSubForm?: any;
	setSubForm2?: any;
	metadata?: any;
	viewOnly?: boolean;
}

const defaultProps: CategorySubFormProps = {
	formHeader: null,
	rows: [],
	setRowMetadata: (a, b) => console.log("setRowMetadata", a, b),
	setRowLimits: (a, b) => console.log("setRowMetadata", a, b),
	setRowSelection: (e) => console.log("setRowSelection", e),
	setSubForm: (e) => console.log("setSubForm", e),
	setSubForm2: (e) => console.log("setSubForm", e),
	subFormNode: null,
	viewOnly: false
};

export const RoomAndBoardForm: React.FC<CategorySubFormProps> = (
	props: CategorySubFormProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly, subFormNode, setSubForm2 } = props;

	let metadata: any = {};
	let custom_metadata: any = {};
	let limits: any[] = [];
	let index : number = -1;
	let PECIndex : number = -1;

	if (subFormNode) {
		if (subFormNode['metadata']) {
			metadata = subFormNode['metadata'];		
		}
		if (subFormNode['custom_metadata']) {
			custom_metadata = subFormNode['custom_metadata'];		
		}
		if (subFormNode['limits']) {
			limits = subFormNode['limits'];

			let found = limits.findIndex ((item) => {
				return ["ABL","MBL"].includes(item.type);
			});

			index = found;

			found = limits.findIndex ((item) => {
				return ["PEC"].includes(item.type);
			});

			PECIndex = found;
		}
	}

	const handleChangeLimit = (e, isPEC = false) => {
		const name = e.target.name;
		let value = e.target.value;

		let index: number = -1;
		let limits: any[] = [];

		if (name === 'limit') {
			value = parseFloat(value);
		} 
		
		if (subFormNode && subFormNode['limits']) {
			limits = subFormNode['limits'];
		}

		if (isPEC) {
			index = limits.findIndex ((item) => {
				return ["PEC"].includes(item.type);
			});
		} else {
			index = limits.findIndex ((item) => {
				return ["ABL","MBL"].includes(item.type);
			});
		}

		if (index > -1) {
			setSubForm2(name, value, "limits", index);
		} else {
			let input = {
				type: 'ABL',
				limit: 0,
				limit_unit: 'PHP',
				daily_limit: 0,
			};

			if (isPEC) {
				input['type'] = 'PEC';
			} 

			input[name] = value;

			let newLimits = [...limits];

			newLimits.push(input);

			setSubForm2(name, newLimits, "limits");
		}
	}

	const columns1: any = [
		{ name: 'name', title: 'Category' },
		{ 
			name: 'rollover', 
			title: 'Rollover',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.rollover 
								 	? row.custom_metadata.rollover : []),
		},
		{ 
			name: 'refresh', 
			title: 'Refresh',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.refresh 
								 	? row.custom_metadata.refresh : []),
		},
	];

	const formattedColumns1 = {
		rollover: ['rollover'],
		refresh: ['refresh'],
	};

	const columnExtensions1 = [
		{ columnName: 'rollover', width: 150 },
		{ columnName: 'refresh', width: 150 },
	];

	const fakeRows2 = [
		{
			category: metadata && metadata.involuntary_upgrading_title ? metadata.involuntary_upgrading_title : '',
			description: metadata && metadata.involuntary_upgrading_description ? metadata.involuntary_upgrading_description : '',
		},
		{
			category: metadata && metadata.voluntary_upgrading_title ? metadata.voluntary_upgrading_title : '',
			description: metadata && metadata.voluntary_upgrading_description ? metadata.voluntary_upgrading_description : '',
		},
	];

	const columns2: object[] = [
		{ name: 'category', title: 'Category' },
		{ name: 'description', title: 'Description' },
	];

	const formattedColumns2 = {
		html: ['description']
	};

	const columnExtensions2 = [
		{ columnName: 'category', width: 240, wordWrapEnabled: true },
		{ columnName: 'description', wordWrapEnabled: true },
	];

	let PECComboField: any = null;

	if (PECIndex > -1 && limits[PECIndex]['limit_unit'] === 'Percentage') {
		PECComboField = PercentField;
	} else if (PECIndex > -1 && limits[PECIndex]['limit_unit'] === 'Months') {
		PECComboField = WaitingPeriodField;
	} else {
		PECComboField = CurrencyField;
	}

	const modeOfPayment = {
		annual: "Annual",
		semiannual: "Semi-Annual",
		quarterly: "Quartly",
		monthly: "Monthly",
	};

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}

					<Grid item container sm={8}>
						<Grid item sm={6}>
							{ !viewOnly ?
								<React.Fragment>
									<InputLabel htmlFor="type">Choose Limit Type</InputLabel>
									<FormControl margin="normal">
										<Select
											onChange={(e) => handleChangeLimit(e, false)}
											value={index > -1 && limits[index]['type'] ? limits[index]['type'] : 'ABL'}
											name="type"
											input={<OutlinedInput labelWidth={0} name="type" id="limit" />}
										>
											<MenuItem value="ABL">ABL</MenuItem>
											<MenuItem value="MBL">MBL</MenuItem>
										</Select>
									</FormControl>
								</React.Fragment> : 
								<ViewField 
									label="Limit Type"
									value={index > -1 && limits[index]['type'] ? limits[index]['type'] : ''}
								/>
							}
						</Grid>
						<Grid item sm={6}>
							{ !viewOnly ?
								<CurrencyField 
									id="limit"
									label="Specify Limit" 
									fieldName="limit"
									selectName="limit_unit"
									fieldValue={
										index > -1 && limits[index]['limit'] ? limits[index]['limit'] : 0
									}
									fieldOnChange={(e) => handleChangeLimit(e, false)}
									disableSelect
								/> :
								<ViewField 
									label="Limit"
									value={
										index > -1 && limits[index]['limit'] && limits[index]['limit_unit'] ? 
										limits[index]['limit'] + ' ' + limits[index]['limit_unit'] : ''
									}
								/>
							}
						</Grid>
						<Grid item sm={6}>
							{ !viewOnly ?
								<React.Fragment>
									<InputLabel htmlFor="limit_unit">Choose PEC Limit Type</InputLabel>
									<FormControl margin="normal">
										<Select
											onChange={(e) => handleChangeLimit(e, true)}
											value={PECIndex > -1 && limits[PECIndex]['limit_unit'] ? limits[PECIndex]['limit_unit']	 : 'PHP'}
											name="limit_unit"
											input={
												<OutlinedInput labelWidth={0} name="limit_unit" id="pecLimit" />
											}
										>
											<MenuItem value="PHP">Peso</MenuItem>
											<MenuItem value="Percentage">Percentage</MenuItem>
											<MenuItem value="Months">Time-based</MenuItem>
										</Select>
									</FormControl>
								</React.Fragment> : 
								<ViewField 
									label="PEC Limit Type"
									value={PECIndex > -1 && limits[PECIndex]['type'] ? limits[PECIndex]['type'] : ''}
								/>
							}
						</Grid>
						<Grid item sm={6}>
							{ !viewOnly ?
								<PECComboField 
									id="limit"
									label="Specify Limit" 
									fieldName="limit"
									selectName="limit_unit"
									fieldValue={
										PECIndex > -1 && limits[PECIndex]['limit'] ? limits[PECIndex]['limit'] : 0
									}
									fieldOnChange={(e) => handleChangeLimit(e, true)}
									disableSelect
								/> :
								<ViewField 
									label="PEC Limit"
									value={
										PECIndex > -1 && limits[PECIndex]['limit'] && limits[PECIndex]['limit_unit'] ? 
										limits[PECIndex]['limit'] + ' ' + limits[PECIndex]['limit_unit'] : ''
									}
								/>
							}
						</Grid>	
					</Grid>

					<Grid item sm={4}>
						{ !viewOnly ?
							<React.Fragment>
								<InputLabel htmlFor="mode_of_payment">Mode of Payment</InputLabel>
								<FormControl component="fieldset" margin="normal">
									<RadioGroup name="mode_of_payment">
										<FormControlLabel
											value="annual"
											control={
												<Radio 
													checked={custom_metadata['mode_of_payment'] ? custom_metadata['mode_of_payment'] === 'annual' : false} 
													name="mode_of_payment"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("mode_of_payment", "annual");
													}}
												/>
											}
											label={modeOfPayment['annual']}
										/>
										<FormControlLabel
											value="semiannual"
											control={
												<Radio 
													checked={custom_metadata['mode_of_payment'] ? custom_metadata['mode_of_payment'] === 'semi-annual' : false} 
													name="mode_of_payment"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("mode_of_payment", "semiannual");
													}}
												/>
											}
											label={modeOfPayment['semiannual']}
										/>
										<FormControlLabel
											value="quarterly"
											control={
												<Radio 
													checked={custom_metadata['mode_of_payment'] ? custom_metadata['mode_of_payment'] === 'quarterly' : false} 
													name="mode_of_payment"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("mode_of_payment", "quarterly");
													}}
												/>
											}
											label={modeOfPayment['quarterly']}
										/>
										<FormControlLabel
											value="monthly"
											control={
												<Radio 
													checked={custom_metadata['mode_of_payment'] ? custom_metadata['mode_of_payment'] === 'monthly' : false} 
													name="mode_of_payment"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("mode_of_payment", "monthly");
													}}
												/>
											}
											label={modeOfPayment['monthly']}
										/>
									</RadioGroup>
								</FormControl>
							</React.Fragment> : 
							<ViewField 
								label="Mode of Payment"
								value={modeOfPayment[custom_metadata['mode_of_payment']]}
							/>
						}
					</Grid>
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					rows={rows}
					formattedColumns={formattedColumns1}
					disableSearch
					disablePaging
					selectionType="radio"
					columnExtensions={columnExtensions1}
					columns={columns1}
					{...props}
				/>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					rows={fakeRows2}
					formattedColumns={formattedColumns2}
					disableSearch
					disableSelect
					disablePaging
					columnExtensions={columnExtensions2}
					columns={columns2}
				/>
			</Grid>
		</React.Fragment>
	);
};

export const APEForm: React.FC<CategorySubFormProps> = (
	props: CategorySubFormProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, setSubForm, subFormNode } = props;

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
		}
	}

	const columns: any = [
		{ name: 'parent_name', title: 'Category' },
		{ name: 'name', title: 'Description' },
		{ 
			name: 'eligible_availees', 
			title: 'Eligible Availees',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.eligible_availees 
								 	? row.custom_metadata.eligible_availees : []),
		},
		{ 
			name: 'age_limit', 
			title: 'Age Limit', 
			getCellValue: row => (row.custom_metadata && row.custom_metadata.age_limit 
								 	? row.custom_metadata.age_limit : 0),
		},
		{ 
			name: 'min_availee', 
			title: 'Min. Availee', 
			getCellValue: row => (row.custom_metadata && row.custom_metadata.min_availee 
								 	? row.custom_metadata.min_availee : 0),
		},
		{ 
			name: 'payment', 
			title: 'Payment',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.payment 
								 	? row.custom_metadata.payment : ''),
		},
		{ 
			name: 'required', 
			title: 'Required',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.required 
								 	? row.custom_metadata.required : false),
		},
	];

	const formattedColumns = {
		eligible_availees: ['eligible_availees'],
		age_limit: ['age_limit'],
		required: ['required'],
		number: ['min_availee', 'payment'],
	};

	const columnExtensions = [
		{ columnName: 'parent_name', width: 100 },
		{ columnName: 'name', wordWrapEnabled: true },
		{ columnName: 'age_limit', width: 100 },
		{ columnName: 'min_availee', width: 100 },
		{ columnName: 'payment', width: 150 },
		{ columnName: 'required', width: 125 },
	];

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
				/>
			</Grid>
			<Grid item sm={12}>
				<Typography variant="overline" display="block" gutterBottom>
					Mobile APEs
				</Typography>
				{
					!viewOnly ? 
						<React.Fragment>
							<Typography variant="body1" gutterBottom>
								Min no. of availees in a shift (8 hrs)
							</Typography>
							<OutlinedInput 
								onChange={setSubForm} 
								value={metadata['mobile_ape_availees'] ? metadata['mobile_ape_availees'] : 0}
								type="number" 
								labelWidth={0} 
								name="mobile_ape_availees" 
								id="mobileAPEs" 
							/>

						</React.Fragment> :
						<ViewField 
							label="Min no. of availees in a shift (8 hrs)"
							value={metadata['mobile_ape_availees'] ? metadata['mobile_ape_availees'] : 0}
						/>
				}
			</Grid>
		</React.Fragment>
	);
};

