import * as React from 'react';
// import { Select, MenuItem } from '@material-ui/core';
import Select from 'react-select';
import './component-style.scss';

interface DropdownChoice {
    value: string;
    label: string;
}

interface IProps {
    placeholder?: string;
    diabled?: boolean;
    value: DropdownChoice;
    choices: DropdownChoice[];
    onChange: (value: DropdownChoice) => void;
}

export class DetailedDropdown extends React.Component<IProps> {

    constructor(props: IProps) {
        super(props);
    }

    render(): JSX.Element {
        return (
            <div className='detailed-dropdown'>
                <div className='text'>{this.props.placeholder}</div>
                <div className='autocomplete'>
                    <div className='detail'>{this.props.value.value}</div>
                    <Select
                        className='text-field' 
                        isDisabled={this.props.diabled ? this.props.diabled : false}
                        value={this.props.value}
                        options={this.props.choices} 
                        onChange={(choice: DropdownChoice) => {
                            this.props.onChange(choice);
                        }} />
                </div>
            </div>
        );
    }
}
